import React, { useState } from 'react'
import axios from 'axios';
import { Link } from 'react-router-dom';
import TrainerList from '../../components/TrainerList'


const TrainerWrite = () => {
    const [writeInfo, setWriteInfo] = useState({
        id: '',
        name: '',
        age: '',
        sex: '',
        birth: '',
        height: '',
        weight: '',
        file: '',
        award: '',
        career: '',
        addr: '',
    })

    const inputChange = (e) => {
        console.log(e.target.name)
        console.log(e.target.value)
        setWriteInfo({
            ...writeInfo,
            [e.target.name]: e.target.value
        })
    }

    const id = sessionStorage.getItem('id');

    const resTrainerWrite = async () => {
        const loadTrainerWrite = await axios.put(`/api/trainerUpdate/${id}`, writeInfo);
        console.log(loadTrainerWrite.data);
        document.location.href = `/trainerView/${id}`;
    }

    return (
        <div className="board_wrap">
            <div className="board_title">
                <strong>{sessionStorage.getItem('name')}</strong>
                <p>   .</p>
            </div>

            <TrainerList />

            <div className="board_write_wrap">
                <div className="board_write">
                    <div class="info">
                        <dl>
                            <dt></dt>
                            <dd><input type="text" name="name" placeholder=" " autoFocus className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="number" name="age" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd>
                                <input type="radio" id="man" name="sex" value="" checked={writeInfo.sex === ""} onChange={inputChange} />
                                <label for="man"></label>
                                <input type="radio" id="woman" name="sex" value="" checked={writeInfo.sex === ""} onChange={inputChange} />
                                <label for="woman"></label>
                            </dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="date" name="birth" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="number" name="height" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="number" name="weight" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><textarea name="award" placeholder=" " className="cont" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><textarea name="career" placeholder=" " className="cont" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="text" name="addr" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                    </div>
                </div>
                <div className="bt_wrap">
                    <button className="on" onClick={() => resTrainerWrite()}></button>
                    <Link to={"/"}></Link>
                </div>
            </div>
        </div >
    )
}

export default TrainerWrite;