import React from "react";
import axios from "axios";
import { useForm } from "react-hook-form";
import { Modal, Button, Form, Container } from "react-bootstrap";
// import { GoogleLogin } from "react-google-login";
import HorizontalLine from "../HorizonLine";


const SignInModal = ({ show, onHide }) => {

    const { register, handleSubmit, watch, formState: { errors } } = useForm();

    const onSubmit = (data) => {
        console.log(data)
        const req = axios.get(`/api/login/${data.id}&/${data.password}`)
            .then(
                res => {
                    console.log(res);
                    console.log(res.data);
                    if (res.data.length == 0) {
                        alert("   .")
                    } else if (res.data.length > 0) {
                        alert(` ${data.id}`);
                        sessionStorage.setItem('id', data.id);
                        sessionStorage.setItem('name', res.data[0].name)
                        sessionStorage.setItem('image', res.data[0].image)
                        document.location.href = '/';
                    }
                    console.log(data.id);
                    console.log(data.password);
                }
            )
    };

    return (
        <Modal
            show={show}
            onHide={onHide}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Container>
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter"></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form onSubmit={handleSubmit(onSubmit)} method='post'>
                        <Form.Group>
                            <Form.Label></Form.Label>
                            <Form.Control type="text" name="id" placeholder=" " autoFocus className="mb-3 rounded-pill" {...register("id", { required: true })} />
                        </Form.Group>
                        <Form.Group>
                            <Form.Label></Form.Label>
                            <Form.Control type="password" name="password" placeholder=" " className="mb-3 rounded-pill" {...register("password", { required: true })} />
                        </Form.Group>
                        <Form.Group className="d-grid gap-2">
                            <Button block variant="dark" type="submit" className="my-3 rounded-pill">
                                
                            </Button>
                            <Form.Text className="text-muted">
                                  ?
                            </Form.Text>
                            <Button block variant="outline-dark" type="button" className="my-3 rounded-pill">
                                
                            </Button>
                            <HorizontalLine text={"OR"} />
                            {/* <GoogleLogin
                            render={(renderProps) => {
                                return ( */}
                            <Button
                                // onClick={renderProps.onClick}
                                // disabled={renderProps.disabled}
                                block
                                style={{
                                    backgroundColor: "#176BEF",
                                    borderColor: "#176BEF",
                                }}
                            >
                                <i className="fab fa-google"></i>&nbsp; Sign In with Google
                            </Button>
                            {/* );
                            }}
                        /> */}
                        </Form.Group>
                    </Form>
                </Modal.Body>
            </Container>
        </Modal>
    );
};

export default SignInModal;