import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams, Link } from 'react-router-dom';
import MemberList from '../../components/MemberList'


function MemberView() {
    const { id } = useParams();

    const [loadMemberView, setLoadMemberView] = useState([]);

    const resMemberView = async () => {
        const loadMemberView = await axios.get(`/api/memberView/${id}`);
        console.log(loadMemberView.data);
        setLoadMemberView(loadMemberView.data);
    }

    const resMemberDelete = async () => {
        await axios.delete(`/api/memberDelete/${id}`);
        document.location.href = '/memberList';
    }

    useEffect(() => {
        resMemberView()
    }, [])

    const mapViewPost = loadMemberView.map(data => {
        return (
            <div key={data.id} className="board_wrap">
                <div className="board_title">
                    <strong>{data.name}</strong>
                    <p>  .</p>
                </div>

                <MemberList />

                <div className="board_write_wrap">
                    <div className="board_write">
                        <div class="info">
                            <dl>
                                <dt></dt>
                                <dd>{data.name}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.age}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd><dd>{data.sex}</dd></dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.birth}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.height}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.weight}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.period}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.regDate}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.endDate}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.phone}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.email}</dd>
                            </dl>
                        </div>
                    </div>
                    <div className="bt_wrap">
                        <button className="on" onClick={() => document.location.href = `/memberEdit/${data.id}`}></button>
                        <button className="on" onClick={() => resMemberDelete()}></button>
                        <Link to={"/memberList"}></Link>
                    </div>
                </div>
            </div >
        )
    })

    return (
        <div className='memberView'>
            {mapViewPost}
        </div>
    )
}

export default MemberView;