import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import TrainerList from '../../components/TrainerList'


function TrainerView() {
    const { id } = useParams();

    const [loadTrainerView, setLoadTrainerView] = useState([]);
    const resTrainerView = async () => {
        const loadTrainerView = await axios.get(`/api/trainerView/${id}`);
        console.log(loadTrainerView.data);
        setLoadTrainerView(loadTrainerView.data);
    }

    useEffect(() => {
        resTrainerView()
    }, [])

    const mapViewPost = loadTrainerView.map((data, index) => {
        return (
            <div key={index} className="board_wrap">
                <div className="board_title">
                    <strong>{data.name}</strong>
                    <p>  .</p>
                </div>

                <TrainerList />

                <div className="board_write_wrap">
                    <div className="board_write">
                        <div class="info">
                            <dl>
                                <dt></dt>
                                <dd>{data.name}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.age}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.sex}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.birth}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.height}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.weight}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.phone}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.email}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.award}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.career}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.addr}</dd>
                            </dl>
                            <dl>
                                <dt></dt>
                                <dd>{data.image}</dd>
                            </dl>
                        </div>
                    </div>
                    <div className="bt_wrap">
                        <button className="on" onClick={() => document.location.href = `/trainerEdit/${id}`}></button>
                    </div>
                </div>
            </div >
        )
    })

    return (
        <div className='trainerView'>
            {mapViewPost}
        </div>
    )
}

export default TrainerView; 