import React, { useEffect, useState } from 'react';
import { Navbar, Nav, Button, Container, Dropdown } from "react-bootstrap";
import SignUpModal from "../modals/SignUpModal";
import SignInModal from "../modals/SignInModal";
import Notification from "../../pages/notification/Notification";
import MenuIcon from '@mui/icons-material/Menu';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';


const Header = () => {
    const [signUpModalOn, setSignUpModalOn] = useState(false);
    const [signInModalOn, setSignInModalOn] = useState(false);
    const [notificationOn, setNotificationOn] = useState(false);

    const id = sessionStorage.getItem('id');

    const logOut = () => {
        const logout = window.confirm(" ?");
        if (logout === true) {
            sessionStorage.clear();
            document.location.href = '/';
        } else {
            document.location.href = '/';
        }
    }

    useEffect(() => {
        if (id === null) {
            setSignInModalOn(true)
        }
    }, [])

    return (
        <>
            <SignUpModal
                show={signUpModalOn}
                onHide={() => setSignUpModalOn(false)}
            />
            <SignInModal
                show={signInModalOn}
                onHide={() => setSignInModalOn(false)}
            />
            <Notification
                show={notificationOn}
                onHide={() => setNotificationOn(false)}
            />

            <header>
                <Navbar bg="light" expand="lg">
                    <Container>
                        <Navbar.Brand><Nav.Link href="/">Trainer</Nav.Link></Navbar.Brand>
                        <Navbar.Toggle aria-controls="basic-navbar-nav" />
                        <Navbar.Collapse id="basic-navbar-nav">
                            <Nav className="me-auto">
                                <Nav.Link href="/memberList"></Nav.Link>
                                <Nav.Link href="/feedbackWorkout/1"></Nav.Link>
                                <Nav.Link href="/QnAList">QnA</Nav.Link>
                                <Nav.Link href="/trainerWrite"></Nav.Link>
                            </Nav>
                            {id ? (
                                <Nav>
                                    <Nav.Link>
                                        {id} 
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Dropdown>
                                            <Dropdown.Toggle variant="dark" className="rounded-pill">
                                                <MenuIcon />
                                                <AccountCircleIcon />
                                            </Dropdown.Toggle>

                                            <Dropdown.Menu>
                                                <Dropdown.Item href="#" onClick={() => setNotificationOn(true)}></Dropdown.Item>
                                                <Dropdown.Item href="#" onClick={() => logOut()}></Dropdown.Item>
                                            </Dropdown.Menu>
                                        </Dropdown>
                                    </Nav.Link>
                                </Nav>
                            ) : (
                                <Nav>
                                    <Nav.Link>
                                        <Button variant="outline-dark" className="rounded-pill" onClick={() => setSignInModalOn(true)}>
                                            
                                        </Button>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Button variant="outline-dark" className="rounded-pill" onClick={() => setSignUpModalOn(true)}>
                                            
                                        </Button>
                                    </Nav.Link>
                                </Nav>
                            )}
                        </Navbar.Collapse>
                    </Container>
                </Navbar>
            </header>
        </>
    );
};

export default Header;