import React, { useState } from 'react'
import axios from 'axios';
import { Link } from 'react-router-dom';
import MemberList from '../../components/MemberList'


function MemberWrite() {
    const [writeInfo, setWriteInfo] = useState({
        name: '',
        age: '',
        sex: '',
        birth: '',
        height: '',
        weight: '',
        period: '',
        regDate: '',
        endDate: '',
        phone: '',
        email: '',
    })

    const inputChange = (e) => {
        console.log(e.target.name)
        console.log(e.target.value)
        setWriteInfo({
            ...writeInfo,
            [e.target.name]: e.target.value
        })
    }

    const resMemberWrite = async () => {
        const loadMemberWrite = await axios.post('/api/memberWrite', writeInfo);
        console.log(loadMemberWrite.data);
        document.location.href = '/memberList';
    }

    return (
        <div className="board_wrap">
            <div className="board_title">
                <strong> </strong>
                <p>   .</p>
            </div>

            <MemberList />

            <div className="board_write_wrap">
                <div className="board_write">
                    <div class="info">
                        <dl>
                            <dt></dt>
                            <dd><input type="text" name="name" placeholder=" " autoFocus className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="number" name="age" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd>
                                <input type="radio" id="man" name="sex" value="" checked={writeInfo.sex === ""} onChange={inputChange} />
                                <label for="man"></label>
                                <input type="radio" id="woman" name="sex" value="" checked={writeInfo.sex === ""} onChange={inputChange} />
                                <label for="woman"></label>
                            </dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="date" name="birth" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="number" name="height" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="number" name="weight" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="text" name="period" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="date" name="regDate" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="date" name="endDate" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="tel" name="phone" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="email" name="email" placeholder=" " className="mb-3" onChange={inputChange} /></dd>
                        </dl>
                        <dl>
                            <dt></dt>
                            <dd><input type="file" name="file" /></dd>
                        </dl>
                    </div>
                </div>
                <div className="bt_wrap">
                    <button className="on" onClick={() => resMemberWrite()}></button>
                    <Link to={"/memberList"}></Link>
                </div>
            </div>
        </div >
    )
}

export default MemberWrite;