import React from 'react'
import { Link, useParams } from 'react-router-dom';


const MemberList = () => {
    const { id } = useParams();

    return (
        <nav className="board_list">
            <ul>
                <li align='right'><button className="bt_member" onClick={() => document.location.href = '#'}><img src="/img/ic_member.png" alt=""></img></button></li>
                <li align='right'><Link to={`/memberView/${id}`}></Link></li>
                <li align='right'><Link to={`/memberInbody/${id}`}></Link></li>
                <li align='right'><Link to={`/feedbackWorkout/${id}`}></Link></li>
                <li align='right'><Link to={`/feedbackDiet/${id}`}></Link></li>
            </ul>
        </nav>
    )
}

export default MemberList;